import { useLocation } from "react-router-dom/cjs/react-router-dom.min";
import FooterHome from "../../components/FooterHome";
import FormContact from "../../components/FormContact/FormContact";
import InfoContact from "../../components/Section/InfoContact";
import HeaderHome from "../../components/header/HeaderHome";
import queryString from 'query-string';

import './DigitalConversionContact.scss'
import { useState } from "react";

function DigitalConversionContact() {

    const { search } = useLocation();
    const { p, s } = queryString.parse(search);
    let title = 'Gi chuyn i s doanh nghip';

    if (p === 'B1') {
        switch (+s) {
            case 0:
                title = 'Gi min ph dnh cho c s y t';
                break;
            case 1:
                title = 'Gi c bn dnh cho c s y t';
                break;
            case 2:
                title = 'Gi nng cao dnh cho c s y t';
                break;
            case 3:
                title = 'Gi ton din dnh cho c s y t';
                break;
            default:
                title = 'Hp tc cng BookingCare';
        }
    }
    if (p === 'B2') {
        switch (+s) {
            case 0:
                title = 'Gi min ph dnh cho doanh nghip';
                break;
            case 1:
                title = 'Gi c bn dnh cho doanh nghip';
                break;
            case 2:
                title = 'Gi nng cao dnh cho doanh nghip';
                break;
            case 3:
                title = 'Gi ton din dnh cho doanh nghip';
                break;
            default:
                title = 'Hp tc cng BookingCare';
        }
    }

        return (
            <>
                <HeaderHome />
                <FormContact title={title} />
                <InfoContact />
                <FooterHome />
            </>
        )
    }

    export default DigitalConversionContact;