
import { useEffect, useState } from 'react';
import './DoctorScheduleRelated.scss';
import { getDoctorClinicDetail } from '../../../../../services/doctorService';
import { useParams } from 'react-router-dom/cjs/react-router-dom.min';
import { useSelector } from 'react-redux';

function DoctorScheduleRelated({ id }) {
    const [isDetail, setIsDetail] = useState(false);
    const [isShowInsurance, setIsShowInsurance] = useState(false);
    const [nameClinic, setNameClinic] = useState('');
    const [addressClinic, setAddressClinic] = useState('');
    const [province, setProvince] = useState('');
    const [price, setPrice] = useState('');
    const [methodPayment, setMethodPayment] = useState('');
    const [moreInfo, setMoreInfo] = useState('');

    const language = useSelector(state => state.app.language);

    useEffect(() => {
        const fetchClinicData = async () => {
            const res = await getDoctorClinicDetail(+id);
            if (res && res.code === 0) {
                const { nameClinic, addressClinic, note, paymentData, provinceData, priceData } = res?.data;
                setNameClinic(nameClinic);
                setAddressClinic(addressClinic);
                setMoreInfo(note);
                if (language === 'vi') {
                    setPrice(priceData.valueVi);
                    setProvince(provinceData.valueVi);
                    setMethodPayment(paymentData.valueVi);
                } else if (language === 'en') {
                    setPrice(priceData.valueEn);
                    setProvince(provinceData.valueEn);
                    setMethodPayment(paymentData.valueEn);
                }
            }
        }
        fetchClinicData();
    }, [id]);

    return (
        <div className='schedule__related__container'>
            <div className='schedule__address'>
                <h6 className='schedule__title'>A CH PHNG KHM</h6>
                <p className='schedule__name__desc'>{nameClinic}</p>
                <p className='schedule__address__desc'>{`${addressClinic}, ${province}`}</p>
            </div>
            <div className={!isDetail ? 'schedule__price d-flex' : 'schedule__price'}>
                <p className='schedule__title'>GI KHM:</p>
                {!isDetail &&
                    <div className='d-flex'>
                        <span className='schedule__price__desc'> {language === 'vi' ? `${price}` : `${price}$`} </span>
                        <p className='schedule__desc--link' onClick={() => setIsDetail(true)}>Xem chi tit</p>
                    </div>
                }
                {isDetail &&
                    <div className='price__detail__wrapper'>
                        <div className='price__detail'>
                            <div className='price__detail__block'>
                                <div className='price__detail__desc'>
                                    <span className='price__detail__number__title'>Gi khm:</span>
                                    <span className='price__detail__desc__para'>Cha bao gm chi ph chp chiu, xt nghim</span>
                                </div>
                                <p className='price__detail__number__desc'>
                                    {`${price}`}
                                </p>
                            </div>
                            <div className='price__detail__payment'>
                                {`Ngi bnh c th thanh ton chi ph bng ${methodPayment}`}
                            </div>
                        </div>
                        <div>
                            <span className='schedule__title'>GI DCH V LIN QUAN:</span>
                            <div className='price__detail__block__wrapper'>
                                <div className='price__detail__block'>
                                    <div className='price__detail__desc'>
                                        <span className='price__detail__number__title'>Gi khm:</span>
                                        <p className='price__detail__desc__para'>Tng phn tch t bo mu ngoi vi (bng my m laser)</p>
                                    </div>
                                    <p className='price__detail__number__desc'>70.000</p>
                                </div>
                                <div className='price__detail__block'>
                                    <div className='price__detail__desc'>
                                        <span className='price__detail__number__title'>Gi khm:</span>
                                        <p className='price__detail__desc__para'>Siu m  bng</p>
                                    </div>
                                    <p className='price__detail__number__desc'>150.000</p>
                                </div>
                                <div className='price__detail__block'>
                                    <div className='price__detail__desc'>
                                        <span className='price__detail__number__title'>Gi khm:</span>
                                        <p className='price__detail__desc__para'>Chp Xquang khp gi thng, nghing hoc chch (2 phim)</p>
                                    </div>
                                    <p className='price__detail__number__desc'>320.000</p>
                                </div>
                                <div className='price__detail__block'>
                                    <div className='price__detail__desc'>
                                        <span className='price__detail__number__title'>Gi khm:</span>
                                        <p className='price__detail__desc__para'>SChp Xquang xng c tay</p>
                                    </div>
                                    <p className='price__detail__number__desc'>320.000</p>
                                </div>
                                <div className='price__detail__block'>
                                    <div className='price__detail__desc'>
                                        <span className='price__detail__number__title'>Gi khm:</span>
                                        <p className='price__detail__desc__para'>Chp Xquang xng bn ngn tay</p>
                                    </div>
                                    <p className='price__detail__number__desc'>320.000</p>
                                </div>
                                <div className='price__detail__block'>
                                    <div className='price__detail__desc'>
                                        <span className='price__detail__number__title'>Gi khm:</span>
                                        <p className='price__detail__desc__para'>Chp Xquang xng cng chn</p>
                                    </div>
                                    <p className='price__detail__number__desc'>320.000</p>
                                </div>
                            </div>

                        </div>
                        <div className='mb-3'>
                            <p className='schedule__desc--link text-end mt-2 mb-0' onClick={() => setIsDetail(false)}>n bng gi</p>
                        </div>
                    </div>
                }

            </div>
            <div className='schedule__insurance'>
                <div className='d-flex'>
                    <span className='schedule__title'>LOI BO HIM P DNG </span>
                    {!isShowInsurance &&
                        <p className='schedule__desc--link' onClick={() => setIsShowInsurance(true)}>Xem chi tit</p>
                    }
                </div>
                {isShowInsurance &&
                    <div className='insurance__apply'>
                        <div className='insurance__detail__block'>
                            <p className='insurance__detail__block__title'>Bo him y t nh nc </p>
                            <p className='insurance__detail--desc'>Hin ti phng khm cha p dng bo him y t nh nc </p>
                        </div>
                        <div className='insurance__detail__block'>
                            <p className='insurance__detail__block__title'>Bo him bo lnh </p>
                            <p className='insurance__detail--desc'>i vi cc n v bo him khng bo lnh trc tip: Phng khm c xut ho n ti chnh (ho n in t) v h tr bnh nhn hon thin h s</p>
                            <p className='insurance__detail--desc text-info cursor-pointer'>Xem danh sch</p>
                        </div>
                    </div>
                }
                {isShowInsurance &&
                    <p className='schedule__desc--link mt-1 ' onClick={() => setIsShowInsurance(false)}>Thu gn</p>
                }
            </div>
        </div>
    )
}

export default DoctorScheduleRelated;